.menu {
  padding: 6rem;
  background: #f3f3f3;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  @include respond(medium) {
    padding: 2rem;
   }

  &-header {
    text-align: center;
    h3 {
      font-size: $fontStandard * 2.8;
      font-weight: 300;
      span {
        color: $brand-color;
        font-size: $fontStandard * 3.2;
        font-weight: 700;
      }

      
    }
    p {
      font-size: $fontStandard * 1.2;
      font-weight: 300;
      margin-top: $marginStandard * 1.5;
      
      @include respond(medium) {
        font-size: $fontStandard ;
       }
    }
  }
  &__content {
    width: 100%;
    padding: 2rem;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(32rem, 1fr));
    grid-template-rows: auto;
    grid-gap: 1.6rem;
    grid-auto-flow: dense ;
    font-size: $fontStandard;
    @include respond(medium) {
      grid-template-columns: repeat(auto-fit, minmax(24rem, 1fr));
     }
     @include respond(medium) {
      padding: 0;
     }



    &-item {
      background: white;
      padding: $paddingStandard;
     
     & img {
         width: 100%;
     }


      &-title h5 {
        margin-top: $marginStandard;
        font-size: $fontStandard * 1.3;
        font-weight: 300;
      }
    }
  }
}

