.testimonial {
  padding: 6rem;
  color: white;
  display: flex;
  background-image: linear-gradient(
      to right bottom,
      rgba(0, 128, 0, 0.158),
      rgba(255, 166, 0, 0.151)
    ),
    url('../../assets/images/bg2.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: repeat;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  @include respond(small) {
    padding: 4rem;
   }

  &-header {
    text-align: center;
    h3 {
      font-size: $fontStandard * 2.8;
      font-weight: 300;
      span {
        color: $brand-color;
        font-size: $fontStandard * 3.2;
        font-weight: 700;
      }
    }
    p {
      font-size: $fontStandard * 1.2;
      font-weight: 300;
      margin-top: $marginStandard * 1.5;
      @include respond(small) {
        font-size: $fontStandard;
        margin-bottom: $marginStandard * 1.5;
       }
    }
  }
  &__content {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(24rem, 1fr));
    grid-template-rows: auto;
    grid-gap: 1.6rem;
    font-size: $fontStandard;
    @include respond(small) {
      padding: 0;
     
     }

    &-item {
      padding: $paddingStandard * 2;
      background: white;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
     


      &-img {
        height: 10rem;
        width: 10rem;
        border-radius: 100%;
        @include respond(small) {
          
          margin-top: $marginStandard * 1.5;
         }
      }

      &-text p {
        margin-top: $marginStandard * 1.5;
        font-size: $fontStandard * 1.2;
        font-weight: 200;
        font-style: italic;
        line-height: 4rem;
        color: black;
        text-align: center;

      }

      &-title h5 {
        margin-top: $marginStandard;
        font-size: $fontStandard * 1.3;
        font-weight: 400;
        color: $brand-color;
      }
    }
  }
}

