.about {
  padding: 6rem;
  font-size: $fontStandard;
  @include respond(medium) {
    padding: 2rem 3rem;
   }

  &__content{
    display: flex;

    @include respond(medium) {
     flex-direction: column;
    }

    &-textBox{
      width: 50%;
      @include respond(medium) {
        width: 100%;
       }
    }

    &-title{
      h3 {
        font-size: $fontStandard * 2.8;
        font-weight: 300;
        span {
          color: $brand-color;
          font-size: $fontStandard * 3.2;
          font-weight: 700;
        }
      }
  }

  &-text{
    p {
      font-size: $fontStandard * 1.2;
      font-weight: 300;
      margin-top: $marginStandard * 1.5;
      @include respond(medium) {
        margin-top: 0;
        font-size: $fontStandard;
       }
    }
  }
  }
  &__img {
    margin-top: $marginStandard *3;
    margin-left: $marginStandard *3;
    align-self: center;
    background:url('../../../src/assets/images/img1.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 50%;
    height: 40rem;


    @include respond(medium) {
      margin-left: 0;
      width: 100%;
     }
    
  }

}