.hero {
    min-height: 100vh;
    background-image: linear-gradient(to right bottom, rgba(0, 128, 0, 0.158), rgba(255, 166, 0, 0.151)), url('../../assets/images/bg.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: repeat;
    display: flex;
    align-items: center;
    justify-content: center;

    @include respond(medium) {
        min-height: 100vh;
       }


    &__content {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        @include respond(small) {
            margin-top: $marginStandard * 8;
           }

        &-title h1 {
            font-size: 5.5rem;
            font-weight: 600;
            color: $color-white;
            text-align: center;
            letter-spacing: .5rem;
            margin-bottom: 2rem;
            @include respond(medium) {
                font-size: 3.5rem;
                padding: $paddingStandard;
               }

        }
        

    
    &-main {
        font-size: 2rem;
        font-weight: 200;
        color: white;
        max-width: 70%;
        text-align: center;
        @include respond(medium) {
            font-size: $fontStandard;
           }

        



    }
    }

    &__Btn{
        margin-top: 3rem;
        font-size: 2rem;
        font-weight: 300;
        text-transform: uppercase;
        text-decoration: none;
        letter-spacing: .5rem;
    }

}