.contact {
  padding: 6rem;
  background: url('/src/assets/images/bg3.jpg');
  background-size: cover;
  @include respond(medium) {
    padding: 1.5rem 2rem;
   }



  &-header {
    text-align: center;
    h3 {
      font-size: $fontStandard * 2.8;
      font-weight: 300;
      span {
        color: $brand-color;
        font-size: $fontStandard * 3.2;
        font-weight: 700;
      }
    }
    p {
      font-size: $fontStandard * 1.2;
      font-weight: 300;
      margin-top: $marginStandard * 1.5;
      @include respond(medium) {
        font-size: $fontStandard;
       }
      
    }
  }

  &__form{
    padding: 7.5rem 5rem;
    background: #fff;
    box-shadow: 0 1.5rem 5rem rgba(0,0,0,0.1);
    max-width: 50rem;
    margin-top: 5rem;

    @include respond(medium) {
      padding: 1.5rem 2rem;
     }

    h4{
      color: #111;
      font-size: 2.3rem;
      font-weight: 400;
      margin-bottom: $marginStandard;
    }

    &-input input, textarea{
      width: 100%;
      margin-bottom: $marginStandard;
      padding: $paddingStandard;
      border: 1px solid #555;
      color: #111;
      outline: none;
      font-size: $fontStandard;
      font-weight: 300;
      resize: none;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      
    }
   

  }
  &__content {
    padding: 6rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: auto;
    grid-gap: 1.6rem;
    font-size: $fontStandard;

   

    &-item {
      padding: $paddingStandard * 2;
      background: white;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      &-img {
        height: 10rem;
        width: 10rem;
        border-radius: 100%;
      }

      &-text p {
        margin-top: $marginStandard * 1.5;
        font-size: $fontStandard * 1.2;
        font-weight: 200;
        font-style: italic;
        line-height: 4rem;
        color: black;
        text-align: center;

      }

      &-title h5 {
        margin-top: $marginStandard;
        font-size: $fontStandard * 1.3;
        font-weight: 400;
        color: $brand-color;
      }
    }
  }
}

.img-1{
    background:url('../../assets/images/testi1.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
.img-2{
    background:url('../../assets/images/testi2.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
.img-3{
    background:url('../../assets/images/testi3.jpg');
    background-position: center;
