// -----------------------------------------------------------------------------
// This file contains all styles related to the footer of the site/application.
// -----------------------------------------------------------------------------
.copyright{
    background-color: $brand-color;
    padding: $paddingStandard;
    color: white;
    max-width: 100vw;
    @include respond(small) {
        padding: 1rem;
       }
    
    &Main{
        display: flex;
        justify-content: space-between;
        @include respond(small) {
            display: flex;
            flex-direction: column;
           }
        &__item{
            margin-bottom: $marginStandard;
            .title{
                font-size: $fontStandard;
                font-weight: 400;
            }
            .subtitle{
                font-size: $fontStandard*0.8;
                font-weight: 300;
                @include respond(medium) {
                    width: 80%;
                   }
            }
        }
    }

}

.block-item{
    padding-left: $paddingStandard * 3;
    display: flex;
    margin-bottom: $marginStandard;
    
    @include respond(medium) {
        padding-left: 0;
       }
      
    &-icon{
        margin-right: $marginStandard;
        color: $color-blue;
        font-size: $fontStandard;
    }
    &-content h5{
        font-size: $fontStandard * 0.8;
        font-weight: 400;
    }
    &-content p{
        font-size: $fontStandard *0.6;
        font-weight: 300;

        
    }
}



.block-item-social{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50%;
    
}
.icon {     
    margin-right: $marginStandard;
    height: 4rem;
    width: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    @include respond(medium) {
        height: 3rem;
        width: 3rem;
       }
}

.box1{
    background-color: #1877f2;
}
.box2{
    background-color: #1da1f2;
}
.box3{
    background-color: #c32aa3;
}
.box4{
    background-color: #25d366;
}

.fab{
    font-size: $fontStandard;
}


.copyrightText{
    font-size: $fontStandard *0.6;
    text-align: center;
    font-weight: 300;
}

.copyrightText a{
    text-decoration: none;
    color: $color-white;
    font-weight: 400;
}